import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

import Link from 'components/Link'
import Section from '../IntersectedSection'

/*
TO CHECK
*/

const MoreInfo = ({ className, ...props }) => (
  <Section
    title="More stuff"
    bgColor="#f3efe0"
    hdColor="#22a39f"
    className={className}
    {...props}
  >
    <div className="columns">
      <div className="column is-6">
        <Link href="/projects.html">
          <i className="fas fa-code-branch" />
          <h3>Projects</h3>
          <p>More info</p>
        </Link>
      </div>
      <div className="column is-6">
        <Link href="/publications.html">
          <i className="fas fa-scroll" />
          <h3>Publications</h3>
          <p>More info</p>
        </Link>
      </div>
    </div>
    <div className="columns">
      <div className="column is-6">
        <Link href="/research_projects.html">
          <i className="fas fa-project-diagram" />
          <h3>R&amp;D Projects</h3>
          <p>More info</p>
        </Link>
      </div>
      <div className="column is-6">
        <Link href="/teaching.html">
          <figure className="fas fa-chalkboard-teacher" />
          <h3>Teaching</h3>
          <p>More info</p>
        </Link>
      </div>
    </div>
  </Section>
)

MoreInfo.propTypes = {
  className: PropTypes.string
}

export default styled(MoreInfo)`
  a {
    display: block;
    width: 100%;
    border-radius: 1em;
    padding: 1rem 3rem 1.5rem 5rem;

    figure {
      position: absolute;
      top: 0;
      right: calc(100% + 0.75em);
    }

    h3 {
      color: #000;
      font-weight: bold;
      font-size: 1.5rem;
    }

    p {
      color: #7a7a7a;
      font-size: 1.25rem;
    }
  }

  a:hover {
    background-color: #fafafa;
  }
`
